import axios from 'axios'

const instance = axios.create({
  baseURL: 'https://challenge.thef2e.com/api/thef2e2019/stage6/',
  timeout: 10000,
  headers: {
    Accept: 'application/json',
    Authorization: 'Bearer 9sFvt0FLhAGDZLyF4b3cjVaIDf0cpGi5yHb8ScY60ABP9UUjjFebMGsqvvST'
  }
})

instance.interceptors.response.use(res => {
  return res
}, err => {
  return Promise.reject(err)
})

export default {
  getRooms () {
    return instance.get('/rooms')
  },
  getRoom (id) {
    return instance.get(`/room/${id}`)
  },
  bookRoom (id, booking) {
    return instance.post(`/room/${id}`, booking).catch(function (error) {
      if (error.response) {
        // The request was made and the server responded with a status code
        // that falls out of the range of 2xx
        return error.response
      } else if (error.request) {
        // The request was made but no response was received
        // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
        // http.ClientRequest in node.js
        return error.request
      } else {
        // Something happened in setting up the request that triggered an Error
        return error.message
      }
    })
  },
  clearBooking () {
    return instance.delete('/rooms')
  }
}
